import {
  LOGIN_INFO,
  REGISTER_INFO,
  AUTH_INFO,
  ADD_CART,
  REMOVE_CART,
} from "../_action/types";

export default function userData(state = {}, action) {
  // reducer ,        
  //  state action 

  //action > user_action return 
  //      switch 

  //  state  state 
  //   
  //state = {} >  

  switch (action.type) {
    case LOGIN_INFO:
      return { ...state, loginSuccess: action.payload };

    case REGISTER_INFO:
      return { ...state, register: action.payload };

    case AUTH_INFO:
      return { ...action.payload };

    case ADD_CART:
      if (action.payload) {
        alert("  .");
        return { ...state };
      } else {
        alert(" .");
        return {
          ...state,
          userData: {
            ...state.userData,
            cart: action.payload.cart,
          },
        };
      }

    case REMOVE_CART:
      return {
        ...state,
        userData: {
          ...state.userData,
          cart: action.payload,
        },
      };

    //payload    

    default:
      return state;
  }
}
