import React, { useState } from "react";
import styled from "styled-components";

import SideMenu from "./SideMenu";
import HeaderBtns from "./HeaderBtn";

function Header() {
  const [checkSideMenu, setCheckSideMenu] = useState(
    window.innerWidth >= 800 ? false : true
  );
  const Btns = HeaderBtns();

  //   800  sideMenu 
  //    state    
  //boolean   state     
  window.onresize = () => {
    if (window.innerWidth <= 800) {
      setCheckSideMenu(true);
    } else {
      setCheckSideMenu(false);
    }
  };

  return (
    <HeaderDiv>
      <HeaderSection>
        <HeaderLogo></HeaderLogo>
        {!checkSideMenu && <Btns.HeaderBtn />}
      </HeaderSection>

      <HeaderSection>
        {checkSideMenu ? <SideMenu /> : <Btns.HeaderLogInBtn />}
      </HeaderSection>
    </HeaderDiv>
  );
}

const HeaderDiv = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;

  position: sticky;
  /* backdrop-filter: blur(20px); */
  background-color: rgba(255, 255, 255, 0.3);
  padding: 1rem;
  border-bottom: 2px solid orange;

  top: 0;
  width: 100%;
  height: 50px;
  z-index: 20;
`;

const HeaderLogo = styled.span`
  padding-right: 10px;
  font-size: 25px;
`;

const HeaderSection = styled.div`
  display: flex;
  align-items: center;
`;

export default Header;
