import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { LeftOutlined, HomeOutlined } from "@ant-design/icons";
import { useDispatch } from "react-redux";
import styled from "styled-components";

import RecentView from "../../Components/RecentView";
import Selector from "../../Components/Selector";
import Loading from "../../Components/Loading";
import ModalBase from "../../Components/Modal/ModalBase";
import FooterDetailPage from "../../Components/Footer/FooterDetailPage";

import getTime from "../../hooks/getTime";
import useAxios from "../../hooks/useAxios";
import useModal from "../../hooks/useModal";

import { addCart } from "../../_action/user_action";
import ProductInfo from "./ProductInfo";

function ProductDetail({ user }) {
  const dispatch = useDispatch();
  const nav = useNavigate();

  const [purchasesCount, setPurchasesCount] = useState(1);
  const [cartAddLoading, setCartAddLoading] = useState(false);
  const { id } = useParams();

  //   
  const {
    resData: product,
    loading,
    connectServer: getProduct,
  } = useAxios("/api/product/productDetail");

  //    
  const {
    resData: otherProduct,
    loading: otherLoading,
    connectServer: getOtherProduct,
  } = useAxios("/api/product/productList");

  //  
  const {
    resData: productWriter,
    loading: writerLoading,
    connectServer: getWriter,
  } = useAxios("/api/user/userInfo");

  const { resData: cartAddResponse, connectServer: addProductCart } =
    useAxios("/api/user/addCart");

  const { openModal, contents, setOpenModal, setContents } = useModal();

  //     
  const time = product && getTime(product.createdAt);

  //  hitory 
  useEffect(() => {
    const get = JSON.parse(localStorage.getItem("productHistory"));
    const setLocalData = () => {
      //LocalStorage    
      const filterGet = get.filter((data) => data.id !== product._id);
      //   LocalStorage     
      localStorage.setItem(
        "productHistory",
        JSON.stringify([
          { id: product._id, image: product.image[0].name },
          ...filterGet,
        ])
        //       
      );
    };
    if (loading === false) {
      if (get === null) {
        //LocalStorage    
        localStorage.setItem(
          "productHistory",
          JSON.stringify([{ id: product._id, image: product.image[0].name }])
        );
      } else {
        ///LocalStorage   
        if (get.length === 6) {
          ///LocalStorage   6 
          if (get.filter((data) => data.id === product._id).length === 1) {
            setLocalData();
            //    
          } else {
            get.pop();
            setLocalData();
            //     
          }
        } else {
          setLocalData();
          //LocalStorage  6 
        }
      }
    }
  }, [loading, product]);

  // //  
  useEffect(() => {
    getProduct({ id });
  }, [id]);

  // //   ,     
  useEffect(() => {
    if (product) {
      getWriter({ id: product.writer });
      getOtherProduct({
        filterId: id,
        category: product.category,
      });
      
      const titleName = document.getElementsByTagName("title")[0];
      titleName.innerHTML = product.title;
    }

  }, [product]);

  //   
  const goCheckOut = () => {
    if (user.isAuth === false) {
      setContents({
        title: "  ",
        message: " .",
      });
      setOpenModal(true);
      return;
    }

    if (product.count === 0) {
      setContents({
        title: "",
        message: " .",
      });
      setOpenModal(true);
      return;
    }

    nav("/checkOut", {
      state: {
        product: [
          {
            ...product,
            purchasesCount: purchasesCount,
            totalPrice: product.price * purchasesCount,
          },
        ],
        detail: true,
        totalPrice: product.price * purchasesCount,
      },
    });
  };

  //   
  const onAddCartProduct = () => {
    if (product.count === 0) {
      setContents({
        title: " ",
        message: " .",
      });
      setOpenModal(true);
      return;
    }

    if (user.isAuth === false) {
      setContents({
        title: "  ",
        message: " .",
      });
      setOpenModal(true);
      return;
    }

    setCartAddLoading(true);
    const option = {
      productId: product._id,
      userId: user._id,
      purchasesCount: purchasesCount,
    };

    addProductCart(option);
  };

  //   
  useEffect(() => {
    if (cartAddResponse && cartAddResponse.duplicate) {
      setContents({
        title: "",
        message: "   .",
        cartBtn: true,
      });
      setOpenModal(true);
    }

    if (cartAddResponse && cartAddResponse.duplicate === false) {
      setContents({
        title: "",
        message: "  .",
        cartBtn: true,
      });
      setOpenModal(true);
    }

    setCartAddLoading(false);
  }, [nav, setContents, setOpenModal, cartAddResponse]);

  const OnAddCart = () => {
    //redux
    dispatch(addCart(product._id));
  };

  return (
    <div className="page">
      <RecentView body={true} />

      {cartAddLoading && (
        <CartLoadingDiv>
          <Loading />
        </CartLoadingDiv>
      )}

      <ModalBase
        contents={contents}
        modalOpen={openModal}
        setModalOpen={setOpenModal}
      />

      <BackBtn onClick={() => nav("/")}>
        <LeftOutlined />
        <HomeOutlined />
      </BackBtn>

      {loading ? (
        <Loading />
      ) : (
        <>
          {product.count === 0 ? (
            <SoldOut> .</SoldOut>
          ) : null}

          {/* <ImgCarousel
      data={product.image}
      setModalOpen={setModalOpen}
      setModalImg={setModalImg}
    /> */}

          <Selector ProductDetail={true} arr={product.image} />

          <ProductInfo
            writerLoading={writerLoading}
            productWriter={productWriter}
            product={product}
            time={time}
            otherProduct={otherProduct}
            otherLoading={otherLoading}
          />

          <FooterDetailPage
            product={product}
            userId={user._id}
            purchasesCount={purchasesCount}
            productWriter={productWriter}
            writerLoading={writerLoading}
            setPurchasesCount={setPurchasesCount}
            onAddCartProduct={onAddCartProduct}
            goCheckOut={goCheckOut}
          />
        </>
      )}
    </div>
  );
}

const CartLoadingDiv = styled.div`
  width: 100%;
  height: 100vh;
  z-index: 100;
  top: 0;
  right: 0;
  position: fixed;
`;

const BackBtn = styled.div`
  font-size: 20px;
  width: 40px;
  cursor: pointer;
`;

const SoldOut = styled.div`
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  overflow: hidden;
  z-index: 10;

  top: 40px;
  left: 0;
  width: 100%;
  height: 500px;

  display: flex;
  justify-content: center;
  align-items: center;

  font-size: 1.5rem;
  color: white;
`;

export default ProductDetail;
